export default {
	data() {
		return {
			customTheme: '',
			username: '',
			phone: '',
		}
	},
	onLoad(e) {
		this.customTheme = this.$store.state.customTheme;
	},
	onReady() {
		this.$http({
			url: '/user/identity',
		}).then(res => {
			this.username = res.username;
			this.phone = res.phone
		})
	},
	methods: {
		submit() {
			this.$http({
				url: '/user/complete',
				method: 'PUT',
				data: {
					username: this.username,
					phone: this.phone * 1
				},
				loading: 'none'
			}).then(() => {
				uni.showToast({
					title: '更新成功',
					icon: 'none'
				})
			})
		},
		back() {
			uni.navigateBack({
				delta: 1,
				animationType: 'pop-out',
				animationDuration: 200
			});
		},
		backItem() {
			this.$http({
				url: '/user/complete',
				method: 'PUT',
				data: {
					username: this.username,
					phone: this.phone * 1
				}
			}).then(res => {
				uni.showToast({
					title: '更新成功',
					icon: 'none'
				})
				setTimeout(() => {
					uni.navigateBack({
						delta: 1,
						animationType: 'pop-out',
						animationDuration: 200
					});
				}, 1000)
			})
		},
		backHome() {
			uni.reLaunch({
				url: '../index/index'
			});
		}
	}
}
